.App {
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Typeahead {
  border: 1px solid #ced4da;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.header {
  height: 200px;
  width: 100%;
  text-align: center;
  line-height: 200px;
}

.input-group {
  padding: 20px;
}

.connection-info {
  width: 30%;
  text-align: left;
}

.action-button {
  text-align: left;
  padding: 20px;
}

.section-title {
  text-align: left;
  padding: 20px;
}

.section {
  padding: 20px;
}

.query-response {
  text-align: left;
}

.path-input {
  width: 30%;
}